module.exports = ":host {\n  display: flex;\n  width: 100%;\n  height: 100%;\n  justify-content: center;\n  align-items: center; }\n\n#gallery-frame {\n  width: 40vw;\n  height: 40vh;\n  border: solid .4px;\n  position: relative; }\n\n#gallery-board {\n  width: 80vw;\n  height: 80vh;\n  border: solid .4px green;\n  position: absolute;\n  transition: all .4s;\n  left: -20vw;\n  top: -20vh; }\n\n#gallery-board.esp0 {\n    left: 0;\n    top: 0; }\n\n#gallery-board.esp1 {\n    top: 0;\n    left: -40vw; }\n\n#gallery-board.esp2 {\n    top: -40vh; }\n\n.exhibit {\n  background-size: cover;\n  background-repeat: no-repeat;\n  position: absolute;\n  width: 100px;\n  height: 80px;\n  transition: all .4s ease-out; }\n\n.exhibit.active {\n    transition: all .4s ease-out;\n    width: 50%;\n    height: 50%; }\n\n.exhibit:first-child {\n    top: 0;\n    left: 50%; }\n\n.exhibit.active:first-child {\n    left: 0; }\n\n.exhibit:nth-child(2) {\n    right: 0;\n    top: 0;\n    -webkit-transform: translate(0, 15vh) rotateZ(90deg);\n            transform: translate(0, 15vh) rotateZ(90deg); }\n\n.exhibit.active:nth-child(2) {\n    -webkit-transform: none;\n            transform: none; }\n\n.exhibit:nth-child(3) {\n    bottom: 0;\n    right: 20%;\n    -webkit-transform: rotateZ(180);\n            transform: rotateZ(180); }\n\n.exhibit.active:nth-child(3) {\n    left: 20vw;\n    -webkit-transform: none;\n            transform: none; }\n\n.exhibit:nth-child(5) {\n    top: calc(50% - 40px);\n    left: 0; }\n\n.exhibit:nth-child(4) {\n    top: 30%;\n    left: 30%; }\n\n#control {\n  position: absolute;\n  top: 30px;\n  left: 30px; }\n\n#next {\n  top: 0;\n  left: 10px;\n  position: absolute; }\n\n#prev {\n  top: 0;\n  left: 60px;\n  position: absolute; }\n"